









import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import Layout from '@/components/Layout.vue';
import Tags from '@/components/Money/Tags.vue';
import InputFrom from '@/components/Money/InputFrom.vue';
import Types from '@/components/Money/Types.vue';
import NumberPad from '@/components/Money/NumberPad.vue';
import Tabs from '@/components/Tabs.vue';
import recordTypesList from '@/constans/recordTypesList';

@Component({
    components: {Tabs, NumberPad, Types, InputFrom, Tags, Layout}
})
export default class Money extends Vue {
    get recordList() {
        return this.$store.state.recordList;
    }

    created() {
        this.$store.commit('fetch');
    }

    recordTypesList = recordTypesList;
    record: RecordItem = {
        tags: [], entering: '', type: '-', amount: 0
    };

    onUpdateTags(value: Tag[]) {
        console.log(value);
        this.record.tags = value;
    }

    onUpdateInput(value: string) {
        console.log(value);
        this.record.entering = value;
    }

    onUpdateType(value: string) {
        this.record.type = value;
    }

    onUpdateNumberPad(value: string) {
        console.log(value);
        this.record.amount = parseFloat(value);
    }

    collectRecord() {
        if (!this.record.tags || this.record.tags.length === 0) {
            return window.alert('请选择一个标签');
        }
        if(!this.record.amount||this.record.amount === 0){
            return window.alert('不能为零')
        }
        this.$store.commit('createRecord', this.record);
        if (this.$store.state.currentError === null) {
            window.alert('成功了');
            this.record.entering = '';
        }
    }
}
