import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/lib/clone';
import createId from '@/lib/createId';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        fetchRecord: [],
        currentError:null,
        currentTagError: null,
        tagList: [],
        currentTag: undefined
    } as RootStore,
    mutations: {
        fetch(state) {
            state.fetchRecord = JSON.parse(window.localStorage.getItem('recordList') || '[]');
        },
        setCurrentTag(state,id:string){
                // @ts-ignore
            state.currentTag = state.tagList.filter(t => t.id === id)[0];
            console.log(state.currentTag);
        },
        createRecord(state, record: RecordItem) {
            const record2 = clone(record);
            record2.createAl = new Date().toISOString();
            state.fetchRecord.push(record2);
            store.commit('saveRecord');
        },
        saveRecord(state) {
            window.localStorage.setItem('recordList', JSON.stringify(state.fetchRecord));
        },
        // tag

        fetchList(state) {
            state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
            if(!state.tagList || state.tagList.length === 0){
                store.commit('createList','衣')
                store.commit('createList','食')
                store.commit('createList','住')
                store.commit('createList','行')
            }

        },
        createList(state, name: string) {
            state.currentTagError = null
            const names = state.tagList.map(item => item.name);
            if (names.indexOf(name) >= 0) {
                state.currentTagError = new Error('tag name is duplicated')
                return
            }
            const id = createId().toString();
            state.tagList.push({id: id, name: name});
            store.commit('saveList');
        },
        removeList(state, id: string) {
            let index = -1;
            for (let i = 0; i < state.tagList.length; i++) {
                if (state.tagList[i].id === id) {
                    index = i;
                    break;
                }
            }
            state.tagList.splice(index, 1);
            store.commit('saveList');
            return true;
        },
        updateTagList(state, payload:{id: string, name: string}) {
            const {id,name} = payload
            const idList = state.tagList.map(item => item.id);
            if (idList.indexOf(id) >= 0) {
                const names = state.tagList.map(item => item.name);
                if (names.indexOf(name) >= 0) {
                    window.alert('标签名重复了')
                } else {
                    const tag = state.tagList.filter(item => item.id === id)[0];
                    tag.name = name;
                    store.commit('saveList');
                }
            }
        },
        saveList(state) {
            window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
        }
    },


});

export default store;