


















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Icon from '@/components/Icon.vue';
import InputFrom from '@/components/Money/InputFrom.vue';
import Buttons from '@/components/Buttons.vue';
// import store from '@/store/index2';
@Component({
    components: {Buttons, InputFrom, Icon}
})
export default class EditLabel extends Vue {
    get currentTag() {
        return this.$store.state.currentTag;
    }

    created() {
        this.$store.commit('fetchList')
        const id = this.$route.params.id;
        this.$store.commit('setCurrentTag', id);
        if (!this.currentTag) {
            this.$router.replace('/404');
        }
    }

    updateTag(name: string) {
        if (this.currentTag) {
            this.$store.commit('updateTagList', {id: this.currentTag.id, name: name});
        }
    }

    removeTag() {
        if (this.currentTag) {
            this.$store.commit('removeList', this.currentTag.id);
            this.$router.replace('/labels');
        }
    }
}
