

























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Layout from '@/components/Layout.vue';
import InputFrom from '@/components/Money/InputFrom.vue';
import Types from '@/components/Money/Types.vue';
import Tabs from '@/components/Tabs.vue';
import intervalList from '@/constans/intervalList';
import recordTypesList from '@/constans/recordTypesList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';

@Component({
    components: {Tabs, Types, InputFrom, Layout}
})
export default class Statistics extends Vue {
    get recordList() {
        return (this.$store.state as RootStore).fetchRecord;
    }

    beautifys(string: string) {
        const day = dayjs(string);
        const now = dayjs();
        if (day.isSame(now, 'day')) {
            return '今天';
        } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
            return '昨天';
        } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
            return '前天';
        } else if (day.isSame(now, 'year')) {
            return day.format('M月DD日');
        } else {
            day.format('YYY年MM月DD日');
        }
    }

    tagString(tag: Tag[]) {
        return tag.length === 0 ? '无' : tag.map(t =>t.name).join('，');
    }

    get result() {
        const {recordList} = this;

        const newList = clone(recordList).filter(r => r.type === this.type).sort((a, b) => dayjs(b.createAl).valueOf() - dayjs(a.createAl).valueOf());
        if(newList.length === 0){return [] }
        type GroupList = { title: string, total?: number, items: RecordItem[] }[]
        const groupList: GroupList = [{title: dayjs(newList[0].createAl).format('YYYY-MM-DD'), items: [newList[0]]}];
        for (let i = 1; i < newList.length; i++) {
            const current = newList[i];
            const last = groupList[groupList.length - 1];
            if (dayjs(last.title).isSame(dayjs(current.createAl), 'day')) {
                last.items.push(current);
            } else {
                groupList.push({title: dayjs(current.createAl).format('YYYY-MM-DD'), items: [current]});
            }
        }
        groupList.map(group => {
            group.total = group.items.reduce((sum, items) =>
            {
                console.log(sum);
                console.log(items);
                return  sum + items.amount
            }, 0);

        });
        console.log(groupList);
        return groupList;
    }

    beforeCreate() {
        return this.$store.commit('fetch');
    }

    type = '-';
    interval = 'day';
    intervals = intervalList;
    types = recordTypesList;
}
